import React, {PropTypes} from 'react';
import { connect } from 'dva';
import {Button} from 'antd';
import styles from './Profile.less';

function Profile({profile}) {
  const {realName, picUrl, firm,lawyerDesc,flaLawyerAdvtype,certificate,email,phone,address} = profile;
  return (
    <div className={styles.profiless}>
      <div className={styles.profile_left}>
        <img src={picUrl} className={styles.profile_picture}/>
      </div>
      <div className={styles.profile_right}>
        <p className={styles.lawyer_name}>{realName}</p>
        <p className={styles.lawyer_name}>{phone}</p>
        <div>
          <span> {firm}</span>
          <span>{address}</span>
        </div>
        <div>工作语言:标准普通话</div>
        <div>
          <p className={styles.lawyer_adv_con}>{lawyerDesc}</p>
        </div>
        <div>
          <div className={styles.lawyer_advantage}>业务专长</div>
          <p className={styles.lawyer_adv_con}>{flaLawyerAdvtype}</p>
        </div>
        <div>
          <div  className={styles.lawyer_advantage}>执业特点</div>
          <p className={styles.lawyer_adv_con}>{flaLawyerAdvtype}</p>
        </div>
      </div>
      <div className={styles.clearfix}></div>
    </div>
  );
}

Profile.propTypes = {
  loading: PropTypes.bool
}

function mapStateToProps ({ profile}) {
  return { profile }
}
export default connect(mapStateToProps)(Profile);



